html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, sans-serif;
  font-size: 1.6rem;
  background-color: #1f0a4e;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

img {
  width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  color: #FFFFFF;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.txt-negritas {
  font-weight: 700;
}

.txt-uppercase {
  text-transform: uppercase;
}

.txt-center {
  text-align: center;
}

.posicionador {
  z-index: -1;
  position: absolute;
  width: 100%;
  height: 100%;
}

.esconder {
  display: none;
}

.bg-claro {
  width: 100%;
  background-color: #4618ac;
  padding-top: 3rem;
  padding-bottom: 3rem;
}

.bg-oscuro {
  width: 100%;
  background-color: #1f0a4e;
  padding-top: 3rem;
  padding-bottom: 3rem;
}

.boton-amarillo {
  width: 100%;
  display: inline-block;
  background-color: #f5a623;
  color: #FFFFFF;
  padding: 1rem 1.5rem;
  border: none;
}
.boton-amarillo:hover {
  cursor: pointer;
}

.boton-amarillo-block {
  width: 100%;
  display: block;
  background-color: #f5a623;
  color: #FFFFFF;
  padding: 1rem 1.5rem;
  border: none;
}
.boton-amarillo-block:hover {
  cursor: pointer;
}

@-webkit-keyframes tracking-in-expand {
  0% {
    letter-spacing: -0.5em;
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}
@keyframes tracking-in-expand {
  0% {
    letter-spacing: -0.5em;
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes slide-in-elliptic-top-fwd {
  0% {
    -webkit-transform: translateY(-600px) rotateX(-30deg) scale(0);
    transform: translateY(-600px) rotateX(-30deg) scale(0);
    -webkit-transform-origin: 50% 100%;
    transform-origin: 50% 100%;
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0) rotateX(0) scale(1);
    transform: translateY(0) rotateX(0) scale(1);
    -webkit-transform-origin: 50% 1400px;
    transform-origin: 50% 1400px;
    opacity: 1;
  }
}
@keyframes slide-in-elliptic-top-fwd {
  0% {
    -webkit-transform: translateY(-600px) rotateX(-30deg) scale(0);
    transform: translateY(-600px) rotateX(-30deg) scale(0);
    -webkit-transform-origin: 50% 100%;
    transform-origin: 50% 100%;
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0) rotateX(0) scale(1);
    transform: translateY(0) rotateX(0) scale(1);
    -webkit-transform-origin: 50% 1400px;
    transform-origin: 50% 1400px;
    opacity: 1;
  }
}
@-webkit-keyframes slide-in-top {
  0% {
    -webkit-transform: translateY(-1000px);
    transform: translateY(-1000px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes slide-in-top {
  0% {
    -webkit-transform: translateY(-1000px);
    transform: translateY(-1000px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }
}
.logo img {
  width: 15rem;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.header-derecha {
  padding: 3rem;
  display: flex;
  align-items: center;
  column-gap: 2.5rem;
}
.header-derecha a {
  display: flex;
  align-items: center;
  column-gap: 1rem;
  font-size: 2rem;
}
.header-derecha a p {
  display: none;
}
@media only screen and (min-width: 768px) {
  .header-derecha a p {
    display: inline;
  }
}
.header-derecha .usuario-icono,
.header-derecha .carrito-icono {
  color: #FFFFFF;
  font-size: 2rem;
}

.header-login {
  position: fixed;
}

.login {
  width: 90%;
  max-width: 120rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
}
@media only screen and (min-width: 768px) {
  .login {
    display: grid;
    grid-template-columns: 2fr 1fr;
    align-items: center;
  }
}

.login_formulario {
  width: 100%;
  -webkit-animation: slide-in-elliptic-top-fwd 0.7s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  animation: slide-in-elliptic-top-fwd 0.7s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

.login_bienvenida {
  -webkit-animation: slide-in-top 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  animation: slide-in-top 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  font-size: 3rem;
  margin-bottom: 2rem;
  color: #FFFFFF;
  font-weight: 900;
}
@media only screen and (min-width: 768px) {
  .login_bienvenida {
    margin-bottom: 0;
    font-size: 5rem;
  }
}

.formulario {
  padding: 4rem 1rem;
  background-color: #4618ac;
}
.formulario fieldset {
  border: none;
}
.formulario legend {
  font-size: 2rem;
  font-weight: 700;
  color: #FFFFFF;
}
.formulario .campo {
  display: block;
  width: 100%;
  margin-top: 4rem;
}
.formulario .campo:last-of-type {
  margin-bottom: 4rem;
}
.formulario label {
  font-size: 1.8rem;
  font-weight: 400;
  color: #FFFFFF;
  display: block;
}
.formulario input:not([type=submit]) {
  width: 100%;
  border: none;
  margin-top: 1.5rem;
  padding: 1rem 1.5rem;
  background-color: #39148d;
  color: #FFFFFF;
}

.navegacion {
  width: 100%;
  background-color: #4618ac;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media only screen and (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    justify-content: space-evenly;
  }
}
.navegacion a {
  flex: 1;
  width: 100%;
  text-align: center;
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: 700;
  padding: 2rem 1rem;
  -webkit-animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.61, 0.355, 1) both;
  animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.61, 0.355, 1) both;
}
@media only screen and (min-width: 768px) {
  .navegacion a {
    width: auto;
  }
}
.navegacion a:hover {
  background-color: #3d1596;
}

.barra-busqueda {
  height: 50rem;
  padding-right: 1rem;
  padding-left: 1rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
  align-items: center;
  justify-content: center;
}
.barra-busqueda h1 {
  color: #FFFFFF;
  text-align: center;
}

.barra-busqueda form {
  border: 1px solid #4618ac;
  width: 100%;
  display: grid;
  grid-template-columns: 9fr 1fr;
}
.barra-busqueda form input,
.barra-busqueda form button[type=submit] {
  padding: 1rem;
  background-color: #1f0a4e;
  border: 0;
  color: #FFFFFF;
}
.barra-busqueda form input:focus {
  outline: none;
}
@media only screen and (min-width: 768px) {
  .barra-busqueda form {
    width: 50%;
    max-width: 70rem;
  }
}

.slider {
  display: block;
  margin-bottom: 5rem;
}

.slider-impar {
  background-color: #1f0a4e;
}

.slider-par {
  background-color: #4618ac;
}

.slider-encabezado {
  font-size: 1.8rem;
  color: #FFFFFF;
  margin-bottom: 2rem;
}

.slider-carrusel {
  height: 35rem;
  display: flex;
  position: relative;
}

.bg-claro .carrusel-flecha-izquierda,
.bg-claro .carrusel-flecha-derecha {
  background-color: #4618ac;
}

.bg-oscuro .carrusel-flecha-izquierda,
.bg-oscuro .carrusel-flecha-derecha {
  background-color: #1f0a4e;
}

.carrusel-flecha-izquierda,
.carrusel-flecha-derecha {
  font-size: 3rem;
  padding: 0.5rem;
  cursor: pointer;
  position: absolute;
  z-index: 2;
  top: 40%;
  color: #FFFFFF;
  transition: 0.2s ease-in-out all;
}
.carrusel-flecha-izquierda:hover,
.carrusel-flecha-derecha:hover {
  transform: scale(1.3);
}

.carrusel-flecha-izquierda {
  left: 0;
}

.carrusel-flecha-derecha {
  right: 0;
}

.carrusel {
  width: 100%;
  display: flex;
  align-items: center;
  column-gap: 1rem;
  overflow: hidden;
  scroll-behavior: smooth;
}

.carrusel-card {
  min-width: 45%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-size: 1.8rem;
  color: #FFFFFF;
  font-weight: 700;
  transition: 0.2s ease-in-out all;
}
@media only screen and (min-width: 768px) {
  .carrusel-card {
    min-width: 25%;
  }
}
@media only screen and (min-width: 996) {
  .carrusel-card {
    min-width: 20%;
  }
}
@media only screen and (min-width: 1200px) {
  .carrusel-card {
    min-width: 18%;
  }
}
.carrusel-card:hover {
  transform: scale(1.02);
}
.carrusel-card p.precio {
  color: #329f00;
}
.carrusel-card img {
  max-width: 100%;
  height: auto;
  max-height: 30rem;
  object-fit: cover;
}